class Cookie {
    constructor () {

    }
    parse () {
        try{
            const cookieArr = document.cookie.split(';')
            let cookieMap = {};
            cookieArr.forEach(function(item){
                cookieMap[item.split('=')[0].trim()] = item.split('=')[1].trim()
            })
            return cookieMap
        }catch(e){
            return {}
        }
    }
    set (c_name, value, time, domain) {
        const expires = ((time==null) ? "" : ";expires=" + new Date(new Date().getTime() + Number(time)).toGMTString())
        domain = !domain ? '' : ';domain=' + domain
        document.cookie=c_name+ "=" + escape(value) + domain + expires + ';path=/'
    }
}
export default new Cookie;


