//站点域名

import {env, system} from 'gome-utils-env';
import version from 'gome-utils-app-version';
let host = {};

Object.defineProperty(host, '__root', {
    get: () =>{
        let _host = '';
        if(env.hybrid){
            //混合app
            //国美在线
            if(env.gome){
                if(env.live)
                    _host = 'gome.com.cn';
                else if(env.uat)
                    _host = 'atguat.com.cn';
                else if(env.tslive)
                    _host = 'tslive.com.cn';
                else
                    _host = 'gome.com.cn';
            }else{
                //默认美信
                if(env.live)
                    _host = 'gomeplus.com';
                else if(env.uat)
                    _host = 'uatplus.com';
                else if(env.tslive)
                //瞎写的
                    _host = 'tsliveplus.com';
                else
                    _host = 'gomeplus.com';
            }
        }else{
            if(location.href.match(/gome\.com\.cn|atguat/)){
                if(env.live)
                    _host = 'gome.com.cn';
                else if(env.uat)
                    _host = 'atguat.com.cn';
                else if(env.tslive)
                    _host = 'tslive.com.cn';
                else
                    _host = 'gome.com.cn';
            }else{
                //默认美信
                if(env.live)
                    _host = 'gomeplus.com';
                else if(env.uat)
                    _host = 'uatplus.com';
                else if(env.tslive)
                //瞎写的
                    _host = 'tsliveplus.com';
                else
                    _host = 'gomeplus.com';
            }
        }
        return 'm.' + _host;
    }
});

//协议头
let protocal = '';

if(env.hybrid){
    if(env.gome && system.ios && version >= 75){
        protocal = 'https:';
    }else if(env.gomeplus && system.ios){
        protocal = 'https:';
    }else{
        protocal = 'http:';
    }
}

host.m     = protocal + '//'       + host.__root;
host.prom  = protocal + '//prom.'  + host.__root;
host.jr    = protocal + '//jr.'    + host.__root;
host.hd    = protocal + '//hd.'    + host.__root;
host.v     = protocal + '//v.'     + host.__root;
host.tuan  = protocal + '//tuan.'  + host.__root;
host.u     = protocal + '//u.'     + host.__root;
host.item  = protocal + '//item.'  + host.__root;
host.q     = protocal + '//q.'     + host.__root;
host.cart  = protocal + '//cart.'  + host.__root;
host.pt    = protocal + '//pt.'    + host.__root;
host.club  = protocal + '//club.'  + host.__root;
host.protocal = protocal;

host.service = {}
Object.defineProperty(host.service, '__root', {
    get: () =>{
        let _host = '';
        if(env.hybrid){
            //混合app
            //国美在线
            if(!env.gome){
                //默认美信
                if(env.live)
                    _host = 'mobile.gomeplus.com';
                else if(env.uat)
                    _host = 'mobile.uatplus.com';
                else if(env.tslive)
                //瞎写的
                    _host = 'mobile.tsliveplus.com';
                else
                    _host = 'mobile.gomeplus.com';
                
            }else{
                if(env.live)
                    _host = 'mobile.gome.com.cn';
                else if(env.uat)
                    _host = 'mobile.atguat.com.cn';
                else if(env.tslive)
                    _host = 'mobile.tslive.com.cn';
                else
                    _host = 'mobile.gome.com.cn';
            }
        }else{
            //混合app
            //国美在线
            if(location.href.match(/gome\.com\.cn|atguat/)){
                if(env.live)
                    _host = 'mobile.gome.com.cn';
                else if(env.uat)
                    _host = 'mobile.atguat.com.cn';
                else if(env.tslive)
                    _host = 'mobile.tslive.com.cn';
                else
                    _host = 'mobile.gome.com.cn';
            }else{
                //默认美信
                if(env.live)
                    _host = 'mobile.gomeplus.com';
                else if(env.uat)
                    _host = 'mobile.uatplus.com';
                else if(env.tslive)
                //瞎写的
                    _host = 'mobile.tsliveplus.com';
                else
                    _host = 'mobile.gomeplus.com';
            }
        }
        return _host;
    }
});


host.service.cms = (function(){
    let pre
    if(!env.hybrid && !env.gome){
        pre = 'https://prom-'
        if(!env.live){
            pre = 'http://prom.'
        }
    }else{
        pre = protocal + '//prom-'
    }
    return pre + host.service.__root
})();

function gomeplusRoot(){
    if(env.uat){
        return 'pre.gomeplus.com'
    }else if(env.tslive){
        return 'pre.gomeplus.com'
    }else{
        return 'pro.gomeplus.com'
    }
}

host.service.gomeplus = (function () {
    
    if(env.uat){
        return 'https://api-bs-pre.gomeplus.com'
    }else if(env.tslive){
        return 'https://api-bs-pre.gomeplus.com'
    }else{
        return 'https://api-bs.gomeplus.com'
    }
})()

export {host};
export default host;


