export default {
    //默认情况
    default: function(data){
        return new Promise((resolve, reject) => {
            if(data.isSuccess == 'Y'){
                resolve(data);
            }else if(data.isSuccess == 'N'){
                reject(new Error(data.failReason || data.msgContent || '网络请求错误'));
            }
        })
    }
}


