import {system} from 'gome-utils-env'

let version = -1;
if(system.android && navigator.userAgent.match(/gome/))
    version = navigator.userAgent.match(/gome[plus]{0,4}\/[iphone\/]{0,7}(\d*)/)[1];
else if(system.ios && navigator.userAgent.match(/gome/))
    version = navigator.userAgent.match(/gome[plus]{0,4}\/[iphone\/]{0,7}(\d*)/)[1];

export default version;


